import React from "react";
//simport logo from "./logo.svg";
import "./App.css";
import Header from './layouts/Header';
import Headersocial from './layouts/Headersocial';
import Headerlogo from './layouts/Headerlogo';
import MyModal from './layouts/MyModal';
import { Route, BrowserRouter as Router, Switch/*, Link, Redirect*/ } from "react-router-dom";
import Home from './pages/Home';
import Aboutus from './pages/Aboutus';
import Contactus from './pages/Contactus';
import Gallery from './pages/Gallery';
import Domains from './pages/Domains';
import SubTechnology from './pages/SubTechnology';
import Portfolio from './pages/Portfolio';
import Technologies from './pages/Technologies';
import Testimonial from './pages/Testimonial';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

function App() {

  
    return (
      <Router basename="/wordpress">
        <div>
          <Headerlogo />
          <Header />
          <Headersocial />       
          <MyModal/>
          <div className="rightphace">
              <a href="/#" className="chat-link">
                  <img src={window.template_path+"/react-src/public/images/headphone.png"} alt="Chat icon" />
              </a>
          </div>
        </div>
        <Switch>
          <Route path="/"  exact component={Home}/>
           
          <Route path="/home" component={Home}/>
          
          <Route path="/aboutus" component={Aboutus}/>
          
          <Route path="/contactus">
            <Contactus />
          </Route>
          <Route path="/gallery">
            <Gallery />
          </Route>
          <Route path="/domains/:slug" component={Domains}/>
          <Route path="/subtechnology">
            <SubTechnology />
          </Route>
          <Route path="/portfolio">
            <Portfolio />
          </Route>
          <Route path="/technology/:slug" component={Technologies} />
          
          <Route path="/testimonial">
            <Testimonial />
          </Route>                
        </Switch>
      </Router>  
    );
}

export default App;
