import React from 'react';
import Sofar from '../pages/Sofar';
import Footer from '../layouts/Footer';
import { Link } from "react-router-dom";
import Slider from "react-slick";
import { replacejscssfile } from '../layouts/LoadChangedJS.js';

class Domains extends React.Component{
	constructor(props) {
        super(props);
        this.state = {
          error: null,
          isLoaded: false,  
          domains: [],
          domainsFeatures: [],
          domainsUseCase: [],
          header: '',
          headerDesc: ''
        };        
    }
     
    componentDidMount(){
	
    	document.body.className = 'domain-page';
        const script = document.createElement("script");
        script.src = window.template_path +"/react-src/public/js/domains.js";
        script.async = true;
        document.body.appendChild(script);

        const { match: { params } } = this.props;

        fetch(window.base_path+"/wp-json/wp/v2/domain?slug="+params.slug)
            .then(res => res.json())
            .then(
            (result) => { 
            	
            	this.setState({
                    isLoaded: true,
                    domains: result[0],
                    header: result[0].title.rendered,
                    headerDesc: result[0].content.rendered,
                    domainsFeatures: result[0].acf.features,
                    domainsUseCase: result[0].acf.use_cases
                });

            },
            (error) => {
              this.setState({
                isLoaded: true,
                error
              });
            }
        ); 
    }

	render(){
		var settings = {
            dots: true,
			infinite: true,
			speed: 1500,
			slidesToShow: 3,					
			slidesToScroll: 3,
			responsive: [
		      {
		        breakpoint: 1024,
		        settings: {
		          slidesToShow: 3,
		          slidesToScroll: 3,
		          infinite: true,
		          dots: true
		        }
		      },
		      {
		        breakpoint: 815,
		        settings: {
		          slidesToShow: 2,
		          slidesToScroll: 2
		        }
		      },
		      {
		        breakpoint: 480,
		        settings: {
		          slidesToShow: 1,
		          slidesToScroll: 1
		        }
		      }
		    ]
        };


		return(
			<div className="pagepillDomain" id="pagepiling"> 
				<div className="section" id="section1">
					<div className="header top_pannel">
					  <div className="header-block we_creat_field">

					    <div className="heading">{this.state.header}</div>
					    	
					    	<div dangerouslySetInnerHTML={{__html:this.state.headerDesc}} />
					    
					    
					    <img className="backgroung-image" src={window.template_path+"/react-src/public/images/financial_sector.png"} alt=""/> </div>
					  <div className="mouse-icon"> <a className="animated bounce infinite" href="#page2" data-menuanchor="page2">
					    <img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse"/></a> </div>
					</div>
				</div>

				<div className="section" id="section2"> 

					<section className="domain finicial_report">
					  <div className="container-fluid">
					    <div className="row">
					      <div className="col-lg-8 col-md-7 col-sm-12 domain_left"> 
					        {
					          	Object.keys(this.state.domainsFeatures).map((item, index) => (

							        <div className={"left_container first_pannel pannel_"+index } style={{display: (index===0?'block':'none')}}>
							          <div className="domain_image"> </div>
							          <div className="domain_text">
							            <h4 className="text-left text-uppercase mb-0">
							             {this.state.domainsFeatures[item].title} 
							            </h4>
							            <div className="text-left mb-2">
							              <p>{this.state.domainsFeatures[item].content}</p>
							            </div>
							          </div>
							        </div>
							    ))
							}				  
					
					      </div>
					      <div className="col-lg-4 col-md-5 col-sm-12 domain_item">
					        <ul>
					          <h2 className="text-left text-uppercase mb-2 mt-2 hokey heading_global"> features </h2>

					          <div className="text-left mb-2 animation-element in-view"> <span className="border_bottom_header"></span> </div>
					          {
					          	Object.keys(this.state.domainsFeatures).map((item, index) => (
							        <li className="feature_html" id={"pannel_"+index}><Link to="/#">{this.state.domainsFeatures[item].title}</Link></li>
							    ))
							  }
					        
					        </ul>
					      </div>
					    </div>
					  </div>
					  <div className="mouse-icon"> <a className="animated bounce infinite" href="#page3" data-menuanchor="page3">
					    <img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse"/></a> </div>
					</section>
				</div>

				<div className="section" id="section3"> 

					<section className="love_to_here protfolio_section">
					  <div className="container-fluid">
					    <div className="row">
					      <div className="col-md-7 col-sm-12 mobile-order p-0">
					        <div className="love_form">
					          <form>
					            <h2 className="text-center">Could you send me the portfolio</h2>
					            <div className="form-group">
					              <label>My email id is</label>
					              <input className="form-control"/>
					            </div>
					            <div className="submit_cls">
					              <button type="submit" className="btn btn-primary">send request</button>
					              <div className="text-center mb-2 animation-element in-view send-request"> <span className="border_bottom_header"></span> </div>
					            </div>
					          </form>
					        </div>
					      </div>
					      <div className="col-md-5 col-sm-12 image_field_prot">
					        <div className="slider_testimonial">
					          <div className="user_comment">
					            <div id="myCarousel" className="carousel slide" data-ride="carousel"> 
					              
					              <ol className="carousel-indicators">
					                <li data-target="#myCarousel" data-slide-to="0" className="active"></li>
					                <li data-target="#myCarousel" data-slide-to="1"></li>
					                <li data-target="#myCarousel" data-slide-to="2"></li>
					              </ol>
					              
					              <div className="carousel-inner">
					                <div className="item carousel-item active"> 
					                  <p className="testimonial"> 
					                    <img alt="portfolio" src={window.template_path+"/react-src/public/images/portfolio.png"}/> </p>
					                </div>
					                <div className="item carousel-item">
					                  <p className="testimonial">
					                    <img alt="portfolio" src={window.template_path+"/react-src/public/images/portfolio.png"}/></p>
					                </div>
					                <div className="item carousel-item"> 
					                  <p className="testimonial">
					                    <img alt="portfolio" src={window.template_path+"/react-src/public/images/portfolio.png"}/></p>
					                </div>
					              </div>
					              
					              <a className="carousel-control left carousel-control-prev" href="#myCarousel" data-slide="prev"> <i className="fa fa-angle-left"></i> </a> <a className="carousel-control right carousel-control-next" href="#myCarousel" data-slide="next"> <i className="fa fa-angle-right"></i> </a> </div>
					          </div>
					        </div>
					      </div>
					    </div>
					  </div>
					  <div className="mouse-icon"> <a className="animated bounce infinite" href="#page4" data-menuanchor="page4">
					    <img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse"/></a> </div>
					</section>
				</div>

				<div className="section" id="section4">
					<section className="fintech_cls">
					  <div className="container">
					    <h2 className="text-center text-uppercase mb-0 hokey heading_global"> {this.state.header} USE CASES </h2>
					    <div className="text-center mb-2 animation-element animate_slow in-view"> <span className="border_bottom_header"></span></div>
					    
					    <Slider {...settings} autoplay className="slider responsive usecase">

					    	{
					          	Object.keys(this.state.domainsUseCase).map((item, index) => (
							        <div className="">
								        <div className="fintech"> 
								          <img className="d-block w-100" src={(typeof this.state.domainsUseCase[item].image === "string" )?this.state.domainsUseCase[item].image:''} alt="First slide"/>
								          <h3 className="text-center">{this.state.domainsUseCase[item].title}</h3>
								          <p className="text-center">{this.state.domainsUseCase[item].content}</p>
								        </div>
								      </div>
							    ))
							}					      
					    </Slider>
					  </div>
					  <div className="mouse-icon"> <a className="animated bounce infinite" href="#page5" data-menuanchor="page5">
					    <img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse"/></a> </div>
					</section>
				</div>
				<div className="section" id="section5">
				<Sofar/>
				</div>
				<div className="section" id="section6">
					<section className="love_to_here domainpage_loveto_here">
					  <div className="container-fluid">
					    <div className="row">
					      <div className="col-md-5 col-sm-12">
					        <div className="love_text ">
					          <h2>We'd love to here <span>from you</span></h2>
					        </div>
					      </div>
					      <div className="col-sm-12 col-md-7" style={{background: '#f6eeea'}}>
					        <div className="love_form domainpage-cls">
					          <form>
					            <div className="form-group domainpage-cls">
					              <label>My name is</label>
					              <input className="form-control"/>
					            </div>
					            <div className="form-group">
					              <label>I would like to talk about...</label>
					              <textarea className="form-control rounded-0" id="exampleFormControlTextarea2" rows="2"></textarea>
					            </div>
					            <div className="form-group">
					              <label>Would you email me at</label>
					              <input className="form-control"/>
					            </div>
					            <div className="form-group">
					              <label>or call me at</label>
					              <input className="form-control"/>
					            </div>
					            <div className="submit_cls">
					              <button type="submit" className="btn btn-primary">send request</button>
					              <div className="text-center mb-2 animation-element send-request in-view"> <span className="border_bottom_header"></span> </div>
					            </div>
					          </form>
					        </div>
					      </div>
					    </div>
					  </div>
					  <div className="mouse-icon"> <a className="animated bounce infinite" href="#page7" data-menuanchor="page7">
					    <img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse"/></a> </div>
					</section>
				</div>
				<Footer/>
			</div>
		);
	}
}
export default Domains;