import React from 'react';

class Headerlogo extends React.Component{

	render(){
		return (
			<div className="logo_pannel">
		        <a className="logo" href="https://www.synsoftglobal.com/">
		            <div className="logo-text">SYNSOFT <img src={window.template_path +"/react-src/public/images/logo_new.png"} alt="Logo" /> GLOBAL</div>
		        </a>
		        <div className="nav-icon"> 
			        <span className="child_one"></span> 
			        <span className="child_two"></span> 
			        <span className="child_three"></span> 
		        </div>
		    </div>
		);
	}
}
export default Headerlogo;