import React, { Component } from "react";

class Sofar extends Component {
  constructor(props) {
        super(props);
        this.state = {
          error: null,
          isLoaded: false,
          content_html: [],
        };        
    }
  componentDidMount(){
        fetch(window.base_path+"/wp-json/wp/v2/pages/295")
            .then(res => res.json())
            .then(
            (result) => { 
                this.setState({
                    isLoaded: true,
                    content_html: result.acf,
                });
            },
            (error) => {
              this.setState({
                isLoaded: true,
                error
              });
            }
        ); 

    
    }

  render() {
    return (
       
        <section className="so_far">
          <div className="container-fluid">
            <div className="row">
              <div className="col-md-6 col-lg-6 col-sm-12">
                <div className="sofar_contenet">  
                  <div dangerouslySetInnerHTML={{__html: this.state.content_html.content_html}} />    
                </div>
              </div>
              <div className="col-md-6 col-sm-12 pr-0 pl-0 text-left">
                <div className="sofar_image desktop_view"> 
                <img src={ (typeof this.state.image === "string" )?this.state.image:
                window.template_path+"/react-src/public/images/ball-final-updated.png" } alt="ball-final-updated"/> </div>
              </div>
            </div>
             <div className="mouse-icon"> <a className="animated bounce infinite" href="#page6" data-menuanchor="page6"><img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse"/> </a> </div>
          </div>
        </section>
      
    );
  }
}
export default Sofar;
