import React, { Component } from "react";
import Footer from '../layouts/Footer';
import Sofar from '../pages/Sofar';
import { Link } from "react-router-dom";
import Slider from "react-slick";
import { replacejscssfile } from '../layouts/LoadChangedJS.js';

class Home extends Component {
    constructor(props) {
        super(props);
        this.what_we_do='';         
        this.domainHtml='';  
        this.state = {
          error: null,
          isLoaded: false,
          menus: [],
          banners: [],          
          banners_img: [],          
          domainData: [],
          domains: [],
          testimonialData: [],
          content_html: [],
          techData: [],
        };        
    } 
  
    componentDidMount(){
        
        const script = document.createElement("script");
        script.src = window.template_path +"/react-src/public/js/home.js";
        script.async = true;
        document.body.appendChild(script);

        fetch(window.base_path+"/wp-json/wp/v2/homepage")
            .then(res => res.json())
            .then(
            (result) => { 
                console.log('test', result[0]);

                this.setState({
                    isLoaded: true,
                    banners: result[0].acf.home_page_banner_text,
                    banners_img: result[0].acf.home_page_banner_image,
                    content_html: result[0].acf,
                    domainData: result.domain,
                    techData: result.tech,
                });
               
                fetch(window.base_path+"/wp-json/wp/v2/testimonial") 
                    .then((resp)=> resp.json())
                    .then((testimonial)=>{
                        this.setState({
                            isLoaded: true,
                            testimonialData: testimonial,
                        });
                    });
            },
            (error) => {
              this.setState({
                isLoaded: true,
                error
              });
            }
        );  
    }

    render() { 

        console.log(this.state);
        var settings = {
          dots: true,
          infinite: true,
          speed: 500,
          slidesToShow: 1,
          slidesToScroll: 1 
        };
        const li = [];
        this.state.domainData.map(function(index){
            return li.push(<li className={'domain_li '+index.post_slug} data-img={index.post_image}><Link to={'domains/'+index.post_slug}>{index.post_title}</Link></li>);
        });


        this.domainHtml = <div className="container-fluid">
                        <div className="row">

                            <div className="col-lg-8 col-md-6 col-sm-12 domain_left">
                                <div className="left_container">
                                    <div className="domain_image" style={{background: "url({window.template_path+'/react-src/public/images/insurances.png'})"}}> </div>

                                    <div className="domain_text">
                                        <h2 className="text-left text-uppercase mb-0 hokey"> {this.state.content_html.domain_section_title} </h2>
                                        <div className="text-left mb-2 animation-element in-view"> 
                                        <span className="border_bottom_header"></span> 
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-lg-4 col-md-6 col-sm-12 domain_item ">                  
                                <ul>
                                   {li}
                                </ul>
                            </div>
                        </div>
                        <div className="mouse-icon">
                            <a className="animated bounce infinite" href="#page5" data-menuanchor="page5"><img  src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse" /> </a>
                            </div>
                        </div>
                

                        const testimonialHtml = [];
                        this.state.testimonialData.map(function(index){
                            testimonialHtml.push(<div dangerouslySetInnerHTML={{__html:index.content.rendered}}/>);
                            return testimonialHtml;
                        });




        return (
          <div className="pagepillHome"  id="pagepiling">
            <div className="mobile-hidden">
                <div id="traveler" className="round_circule">
                    <div id="bouncer" className='d-none'> <img alt="index_ball" src={window.template_path+"/react-src/public/images/index_ball.png"}/> </div>
                </div>
                <div id="sides"></div>
            </div>        
            <div className="section" id="section1">
                <div className="header top_pannel">
                    <div className="header-block we_creat_field" >
                        <div dangerouslySetInnerHTML={{__html: this.state.banners?this.state.banners:''}} />
                        <div className="buttons-home"> <a className="button" href="#page2" data-menuanchor="page2">explore</a> </div>
                    </div>
                    <div className="pink-rw"> 
                        <img src={this.state.banners_img.app_futura_image} alt="appfutura logo" /> 
                        <img  src={this.state.banners_img.bark_image} alt="Bark logo" /> 
                        <img  className="upwork-logo" src={this.state.banners_img.upwork_image} alt="Upwork logo" /> 
                        <img  src={this.state.banners_img.clutch_image} alt="Clutch logo" /> 
                        <img  src={this.state.banners_img.crowd_reviews_image} alt="crowd review logo" /> 
                        <img  src={this.state.banners_img.good_firms_image} alt="goodtimes logo" /> 

                    </div>                    
                    <div className="round_circule mobile-img"> 
                    <img  alt="index_ball" src={window.template_path+"/react-src/public/images/index_ball.png"}/></div>
                </div>
            </div>
            <div className="section" id="section2">

                <section className="what_do">
                    <div className="container-fluid">
                        <h2 className="text-center text-uppercase mb-0 hokey letter_pannel">{this.state.content_html.what_we_do_title} </h2>
                        <div className="text-center mb-4 animation-element in-view"> <span className="border_bottom_header"></span> </div>
                        <div className="row">
                        {
                            this.state.techData.map((index) => (
                                (index.post_title.trim() !== "")?
                                (<div className="col-md-4">                                   
                                    <div className="inner_what">
                                        <div className="text-center min-length"> <img alt="w1" src={(typeof index.post_image) ==='string'?index.post_image:''}/> </div>
                                        <div>
                                            <h2 className="text-center text-uppercase mb-3 mt-4 hokey"> {index.post_title} </h2>
                                        </div>
                                        <div className="text-center">
                                            <p>{index.post_content}</p>
                                            <Link to={"/technology/"+index.post_slug}>Read more</Link>
                                        </div>
                                    </div>
                                </div>
                                ):('')
                            ))
                        }
                        </div>
                        <div className="mouse-icon">
                            <a className="animated bounce infinite" href="#page3" data-menuanchor="page3">
                                <img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse" />
                            </a>
                        </div>
                    </div>
                </section>
            </div>
            <div className="section" id="section3">
               <Sofar/>
            </div>
            <div className="section" id="section4">
            {
                <section className="domain">
                    {this.domainHtml}
                </section>
            }
            </div>;
            <div className="section" id="section5"> 
                <section className="testimonial">
                  <div className="container-fluid">
                   <div className="testimonial-center">
                    <div className="row">
                      <div className="col-md-6 p-0">
                        <div className="user_image"> <img src={window.template_path+"/react-src/public/images/testimonial.png"} alt="testimonial"/> </div>
                      </div>
                      <div className="col-md-6 right_testimonial">
                        <div className="slider_testimonial">
                          <h2 className="text-left text-uppercase mb-0 hokey"> TESTIMONIALS </h2>
                          <div className="text-left mb-2 animation-element animate_slow in-view"> <span className="border_bottom_header"></span> 
                          </div>
                          <div className="user_comment">
                            <Slider {...settings} autoplay className="slider responsive">
                                {testimonialHtml}
                            </Slider>
                          </div>
                        </div>
                      </div>
                    </div>
                    </div>
                    <div className="mouse-icon"> <a className="animated bounce infinite" href="#page6" data-menuanchor="page6"><img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse"/> </a> </div>
                  </div>
                </section>
              </div>   
            <div className="section" id="section6">
                <section className="love_to_here">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-md-5 col-sm-12 image_field_prot">
                                <div className="slider_testimonial">
                                    <div className="user_comment">
                                        <div id="myCarousel1" className="carousel slide" data-ride="carousel">
                                            <ol className="carousel-indicators">
                                                <li data-target="#myCarousel1" data-slide-to="0" className=""></li>
                                                <li data-target="#myCarousel1" data-slide-to="1" className=""></li>
                                                <li data-target="#myCarousel1" data-slide-to="2" className="active"></li>
                                            </ol>
                                            
                                            <div className="carousel-inner">
                                                <div className="item carousel-item">
                                                    <p className="testimonial"> <img alt="portfolio" src={window.template_path+"/react-src/public/images/portfolio.png"}/> </p>
                                                </div>
                                                <div className="item carousel-item">
                                                    <p className="testimonial"><img alt="portfolio1" src={window.template_path+"/react-src/public/images/portfolio.png"}/></p>
                                                </div>
                                                <div className="item carousel-item active">
                                                    <p className="testimonial"><img alt="portfolio2" src={window.template_path+"/react-src/public/images/portfolio.png"}/></p>
                                                </div>
                                            </div>
                                            <a className="carousel-control left carousel-control-prev" href="#myCarousel1" data-slide="prev"> <i className="fa fa-angle-left"></i> </a>
                                            <a className="carousel-control right carousel-control-next" href="#myCarousel1" data-slide="next"> <i className="fa fa-angle-right"></i> </a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-7 col-sm-12 description_from align-items-stretch" style={{background: '#f6eeea'}}>
                                <div className="love_form home-form">
                                    <form>
                                        <div className="form-group">
                                            <label>My name is</label>
                                            <input className="form-control"/>
                                        </div>
                                        <div className="form-group">
                                            <label>I would like to talk about...</label>
                                            <textarea className="form-control rounded-0" id="exampleFormControlTextarea2" rows="2"></textarea>
                                        </div>
                                        <div className="form-group">
                                            <label>Would you email me at</label>
                                            <input className="form-control mobile-width"/>
                                        </div>
                                        <div className="form-group">
                                            <label>or call me at</label>
                                            <input className="form-control"/>
                                        </div>
                                        <div className="submit_cls">
                                            <button type="submit" className="btn btn-primary">send request</button>
                                            <div className="text-center mb-2 send-request"> <span className="border_bottom_header"></span> </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                        <div className="mouse-icon">
                            <a className="animated bounce infinite" href="#page7" data-menuanchor="page7"><img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse" /> </a>
                        </div>
                    </div>
                </section>
            </div>
            <Footer/>
        </div>
        );
    }
}
 
export default Home;