import React from 'react';
import Sofar from '../pages/Sofar';
import Footer from '../layouts/Footer';
import Slider from "react-slick";
import { Link } from "react-router-dom";

class Technologies extends React.Component{
  constructor(props) {
        super(props);
        this.state = {
          error: null,
          isLoaded: false,  
          technology: [],
          header: [],
          content: '',
          slider: '',
          webTechSlider: '',
          techAcf: [],
          subtechnology:[]
        };        
    }
     
    componentDidMount(){
        document.body.className = 'technologies_page';
        const script = document.createElement("script");
        script.src = window.template_path +"/react-src/public/js/technology.js";
        script.async = true;
        document.body.appendChild(script);

        const { match: { params } } = this.props;

        fetch(window.base_path+"/wp-json/wp/v2/pages?slug="+params.slug)
            .then(res => res.json())
            .then(
            (result) => { 
              this.setState({
                    isLoaded: true,
                    technology: result[0],
                    header: result[0].title.rendered,
                    content: result[0].content.rendered,
                    slider: result[0].acf.slider,
                    techAcf: result[0].acf,
                    webTechSlider: result[0].acf.web_tech_slider
                });
          fetch(window.base_path+"/wp-json/wp/v2/subtechnology")
            .then(res => res.json())
            .then(
            (result) => { 
              this.setState({
                    isLoaded: true,
                    subtechnology: result,
                });
            },
            (error) => {
              this.setState({
                isLoaded: true,
                error
              });
            }
        ); 
      }
    )
  }

  render(){
    var settings = {
        dots: true,
        infinite: true,
        speed: 1500,
        slidesPerRow: 1,
        rows: 2,
      };
      var webTechsettings = {
        dots: true,
        slidesPerRow: 3,
        rows: 2,
        infinite: true,
        speed: 1500,
      };
      const subTechHtml = [];

      this.state.subtechnology.map(function(index){
        console.log(index);
        return subTechHtml.push(<Link to="/#">{index.title.rendered}</Link>)
      
      })
    return(
      <div id="pagepiling"> 
        
        <div className="section" id="section1">
          <div className="header top_pannel">
            <div className="header-block we_creat_field perra_pannel">
              <div className="heading">{this.state.header}</div>
              <p className="banner_txt">
                {subTechHtml}
              </p>
              
              <img className="backgroung-image" src={(typeof this.state.techAcf.banner_image ==='string')?this.state.techAcf.banner_image:""} alt=""/> </div>
          </div>
          
          <div className="mouse-icon"> <a className="animated bounce infinite" href="#page2" data-menuanchor="page2">
          <img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse" /></a> </div>
        </div>
        
        
        <div className="section" id="section2"> 
          
          <section className="content-box">
            <div className="container-fluid">
              <div className="row">
                <div className="col-md-12 circule_content">
                  <div className="technology-content"  dangerouslySetInnerHTML={{__html:this.state.content}}/>
                </div>
              </div>
            </div>
            <div className="webuilt-img">
            <img src={window.template_path+"/react-src/public/images/webuilt.png"} alt="Zig Zag"/></div>
            <div className="mouse-icon"> <a className="animated bounce infinite" href="#page3" data-menuanchor="page3">
            <img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse"/></a> </div>
          </section>
        </div>
        
        
        <div style={{backgroundColor: '#f6eeea'}} className="section" id="section3">
          <section className="technologies_item">
            <div className="container"> 
              
              <div className="row">
                <div className="col-md-12">
                  <Slider {...settings} autoplay className="slider responsive technology-slider"> 
                  {
                    (typeof this.state.slider!=="undefined")?
                    (Object.keys(this.state.slider).map((item, index) => (
                                          (this.state.slider[item].title.trim() !== "")?
                                          (<div className="">
                                            <div className={(index===1 || index%2===1)?'web_tech flex_direction':'web_tech'}>
                                              <div className="image_items"> 
                                              <img src={(typeof this.state.slider[item].image ==='string')?this.state.slider[item].image:""} alt=""/> </div>
                                              <div className="content_items">
                                                <h2>{this.state.slider[item].title}</h2>
                                                <p>{this.state.slider[item].content}</p>
                                              </div>
                                            </div>
                                          </div>):('')
                    ))):('')           
                  }
                  </Slider>
                </div>
              </div>
              
            </div>
            <div className="mouse-icon"> <a className="animated bounce infinite" href="#page4" data-menuanchor="page4">
            <img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse"/></a> </div>
          </section>
          
        </div>
        
        
        <div style={{backgroundColor: "#f6eeea"}} className="section" id="section4">
          <section className="love_to_here protfolio_section web_tech_stack">
            <div className="container">
              <div className="row">
                <div className="col-md-7 col-lg-8 col-sm-12 tech_stack">
                  <div className="webtech_con">
                    <h2 className="text-center text-uppercase mb-0 hokey heading_global"> {this.state.techAcf.web_tech_section_title} </h2>
                    <div className="text-center mb-2 animation-element animate_slow in-view"> <span className="border_bottom_header"></span> </div>
                    <Slider {...webTechsettings} autoplay className="slider responsive webtech_slider">
                      {
                        (typeof this.state.slider!=="undefined")?
                        (Object.keys(this.state.webTechSlider).map((item, index) => (
                        (this.state.webTechSlider[item].title.trim() !== "")?
                          (<div className="">
                            <div className="technology_field">
                            <img src={(typeof this.state.webTechSlider
                                                    [item].image === 'string')?this.state.webTechSlider
                              [item].image:''} alt="Angular icon"/> </div>
                            <div className="text-center tech_name">{this.state.webTechSlider[item].title}</div>
                          </div>):('')
                        
                        ))):('')
                      }
                    </Slider>
                  </div>
                </div>
                <div className="col-md-5 col-lg-4 col-sm-12 image_field_prot"> <a className="download-img" href="/#" data-toggle="modal" data-target="#myporfolioModal"> 
                <img src={window.template_path+"/react-src/public/images/download.png"} alt="download_image"/> </a> </div>
              </div>
            </div>
            <div className="mouse-icon"> <a className="animated bounce infinite" href="#page5" data-menuanchor="page5">
            <img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse"/></a> </div>
          </section>
        </div>
        <div className="section" id="section5"> 
          <Sofar/>
        </div>
        
        <div className="section" id="section6"> 
          
          
          <section className="love_to_here">
            <div className="container-fluid">
              <div className="row">
                <div className="col-sm-5">
                  <div className="love_text">
                    <h2>We'd love to here <span>from you</span></h2>
                  </div>
                </div>
                <div className="col-sm-7" style={{background: '#f6eeea'}}>
                  <div className="love_form">
                    <form>
                      <div className="form-group">
                        <label>My name is</label>
                        <input className="form-control"/>
                      </div>
                      <div className="form-group">
                        <label>I would like to talk about...</label>
                        <textarea className="form-control rounded-0" id="exampleFormControlTextarea2" rows="2"></textarea>
                      </div>
                      <div className="form-group">
                        <label>Would you email me at</label>
                        <input className="form-control"/>
                      </div>
                      <div className="form-group">
                        <label>or call me at</label>
                        <input className="form-control"/>
                      </div>
                      <div className="submit_cls">
                        <button type="submit" className="btn btn-primary">send request</button>
                        <div className="text-center mb-2 animation-element in-view send-request"> <span className="border_bottom_header"></span> </div>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
            <div className="mouse-icon"> <a className="animated bounce infinite" href="#page7" data-menuanchor="page7">
            <img src={window.template_path+"/react-src/public/images/mouse.png"} alt="mouse"/></a> </div>
          </section>
        </div>
        
        
        <Footer/>
        
        
        
        <div className="modal edit_click fade" id="myModal">
          <div className="modal-dialog">
            <div className="modal-content" style={{background: '#f6eeea'}}> 
              
              
              <div className="modal-header">
                <button type="button" className="close" data-dismiss="modal">
                <img alt="close_click" src="images/close_click.png"/></button>
              </div>
              
              
              <div className="modal-body">
                <div className="love_form">
                  <form>
                    <div className="form-group">
                      <label>My name is</label>
                      <input type="text" className="form-control"/>
                    </div>
                    <div className="form-group">
                      <label>I would like to talk about...</label>
                      <textarea className="form-control rounded-0" id="exampleFormControlTextarea21" rows="2"></textarea>
                    </div>
                    <div className="form-group">
                      <label>Would you email me at</label>
                      <input type="text" className="form-control"/>
                    </div>
                    <div className="form-group">
                      <label>or call me at</label>
                      <input type="text" className="form-control"/>
                    </div>
                    <div className="submit_cls">
                      <button type="submit" className="btn btn-primary">send request</button>
                      <div className="text-center mb-2 send-request"> <span className="border_bottom_header"></span> </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        
        
        
        <div className="modal edit_click fade" id="myporfolioModal">
          <div className="modal-dialog">
            <div className="modal-content" style={{background: '#f6eeea'}}> 
              
              
              <div className="modal-header">
                <button type="button" className="close" data-dismiss="modal">
                <img src="images/close_click.png" alt="close_click" /></button>
              </div>
              
              
              <div className="modal-body">
                <div className="love_form">
                  <form>
                    <h2 className="text-center">Could you send me the portfolio</h2>
                    <div className="form-group">
                      <label>My email id is</label>
                      <input className="form-control"/>
                    </div>
                    <div className="submit_cls">
                      <button type="submit" className="btn btn-primary">send request</button>
                      <div className="text-center mb-2 animation-element in-view send-request"> <span className="border_bottom_header"></span> </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        
        
      </div>
    );
  }
}
export default Technologies;