import React from 'react';
import { /*Route, BrowserRouter as Router, *//*Switch,*/ Link/*, Redirect*/ } from "react-router-dom";

class Footer extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      menus: []
    };
  }
  componentDidMount() {
   
    
    fetch(window.base_path +"/wp-json/menus/v1/menus/4")
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
             isLoaded: true,
             menus: result.items
          });
        },
       
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      );      
  }
    render(){
        const menuHtml = [];
        this.state.menus.map(function(index, key){
            const menuLi = [];
            if(index.child_items){
                index.child_items.map(function(childIndex){
                    const url_slug ='';
                    if(childIndex.type==="domains"){
                        url_slug = "/domains/";
                    }
                    if(childIndex.type==="technology"){
                        url_slug = "/technology/";
                    }
                    menuLi.push(<li><Link to={url_slug+childIndex.slug}>{childIndex.title}</Link></li>)
                });
            }
            menuHtml.push(
                        <div className="col-lg-4 col-md-6">
                            <h4 className="text-left text-uppercase mb-0 hokey"> {index.title} </h4>
                            <ul>
                            {menuLi}
                            </ul>
                            
                        </div>
                      
                    );
        });


        return (
            <div className="section" id="section7" style={{background:'#f6eeea'}}>
                <section className="footer_section">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-md-8">
                                <div className="left_side">
                                    <div className="row">
                                        {menuHtml}
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-4 p-0">
                                <div className="right_side">
                                    <div className="contact_field">
                                        <h3>Mailing Address</h3>
                                        <p>123 Anywhere St., Any City, State, Country 12345</p>
                                    </div>
                                    <div className="contact_field">
                                        <h3>Email Address</h3>
                                        <p><a href="mailto:hello@reallygreatsite.com">hello@reallygreatsite.com</a></p>
                                    </div>
                                    <div className="contact_field">
                                        <h3>Phone Number</h3>
                                        <p> <a href="tel:(123) 456 7890">(123) 456 7890</a></p>
                                    </div>
                                    <div className="contact_field solcial_icon">
                                        <ul>
                                            <li>
                                                <a href="/#"> <img alt="instagram" src={window.template_path +"/react-src/public/images/instagram-black.png"}/> </a>
                                            </li>
                                            <li>
                                                <a href="/#"> <img alt="twitter" src={window.template_path +"/react-src/public/images/twitter-black.png"}/> </a>
                                            </li>
                                            <li>
                                                <a href="/#"> <img alt="linked" src={window.template_path +"/react-src/public/images/linked-in-black.png"}/> </a>
                                            </li>
                                            <li>
                                                <a href="/#"> <img  alt="facebook" src={window.template_path +"/react-src/public/images/facebook.png"}/> </a>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <div className="go_top">
                    <a className="animated bounce infinite" href="#page1" data-menuanchor="page1"><img src={window.template_path+"/react-src/public/images/arrow-up.png"} alt="go to top" /></a>
                </div>
            </div>
        );
    }
} 
export default Footer;