import React from 'react';

class Headersocial extends React.Component{
	render(){
		return (
			<div className="socials">
		        <a href="/#" className="social-btn ig">
		        	<img src={window.template_path +"/react-src/public/images/skype.png"} alt="text"/>
		        </a>
		        <a href="/#" className="social-btn li">
		        	<img src={window.template_path +"/react-src/public/images/phone.png"} alt="text"/>
		        </a>
		        <a href="/#" className="social-btn dr">
		        	<img src={window.template_path +"/react-src/public/images/edit.png"} alt="text" data-toggle="modal" data-target="#myModal"/>
		        </a>
		    </div>
		);
	}
}
export default Headersocial;